@import url(https://fonts.googleapis.com/css?family=PT+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.site-root {
	height: 100%;
	width: 100%;
	padding: 0px;
}

.nav-bar-container {
	width: 100%;	
	height: 50px;
	position: fixed;
	top: 0px;
	background-color: var(--site-dark-grey);
}

.content-container {
	width: 100%;
	height: 100%;
	padding-top: 36px;
	overflow-x: hidden;
}

.nav-title {
	float: left !important;	
	font-size: 1.4em;
	vertical-align: middle;
	text-align: center;
	padding: 16px;
	padding-bottom: 0px;
	font-weight: bold;
}

.nav-title > a {
	text-decoration: none;
	color: var(--site-blue);
}

.nav-link-desktop-container {
	float: left;	
	margin: 0;
	padding: 16px;
	padding-top: 0px;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-left: 30px;
}

.nav-link {
	height: 100%;
	font-weight: bold;
	display: inline-block;
	font-size: 1.2em;
}

.router-link {
	text-decoration: none;
	color: var(--site-orange);
}

.nav-link-flex-container {
	color: white;	
	display: flex;
	align-items: center;
	margin: 18px;
}

@media(max-width: 600px) {
	.nav-bar-container {
		height: 80px;
	}
	.nav-title {
		text-align: center;
	}
	.nav-link-desktop-container {
		margin-left: 0px;	
		padding-left: 0px;
	}

	.content-container {
		margin-top: 50px;
	}
}

.nav-link:hover {
	cursor: pointer;
}

.nav-link > a:hover {
	color: var(--site-tan);
}

.nav-link:hover > .terminal-prompt {
	display: inline;
}

.hide {
	display: none;
}

.projects-container {
	margin-top: 26px;
	/*
	display: flex;	
	flex-direction: column;
	justify-content: space-between;
	*/

	margin-left: 16px;
	margin-right: 16px;

	/*overflow-y: scroll !important;*/
	width: 90%;
	height: 80%;
	overflow: auto;
	color: var(--site-white);
}

.projects-title {
	text-align: center;
	margin-bottom: 0;
}

.project {
	/*
	display: flex;	
	flex-direction: column;
	justify-content: space-between;
	align-items: left;
	*/
	margin-bottom: 10px;
}

.project-content {
	padding-top: 5px;
	padding-left: 16px;	
	overflow-x: hidden;
}

.project-name {
	font-weight: bold;
	font-size: 1.5em;
}

.project-name > a {
	text-decoration: none;
	color: var(--site-white);
	cursor: pointer;
}

.project-name:hover > a {
	color: var(--site-blue);
}

.project-lang {
	color: var(--site-tan);	
	margin-top: 10px;
	font-size: 0.8em;
}

.blog-container {
	color: var(--site-white);
	margin: 16px;
	text-align: center;
}

.about-container {
	margin-left: 16px;
	margin-right: 16px;
	color: var(--site-white);
}

.about-link {
	text-decoration: none;
	color: var(--site-orange);	
	cursor: pointer;
	font-size: 1.2em;
}

.about-link:hover {
	color: var(--site-tan);
}

.about-header {
	color: var(--site-blue);
	text-decoration: underline;
	font-size: 1.5em;
}

.about-description {
	text-align: left;
	max-width: 600px;
	font-size: 1.2em;
}
:root {
  --site-white: #dedede;
  --site-dark-grey: #282c34;
  --site-orange: #dd672c;
  --site-blue: #56b6c2;
  --site-grey: #abb2bf;
  --site-tan: #c7ad9c;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

html {
  background-color: #282c34;
  background-color: var(--site-dark-grey);
}

div {
  font-family: 'PT Mono', monospace;
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
